.TodoList {
  padding: 20px;
  height: 700px;
  width: 40%;

  display: flex;
  flex-direction: column;
  gap: 5px;

  border-radius: 10px;
  border: 2px solid #e9e9e9;
  box-shadow: 0 0 8px 0 rgba(34, 60, 80, 0.2);
  &__controls {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
  }
}